.section-line-rule {
    padding: 2rem;
    &__inner {
        background-color: #fff;
        border-radius: 2.4rem;
        padding: 2rem;
    }
    &__title {
        font-size: 2.6rem;
        text-align: center;
        color: #000;
        line-height: 1.6;
        margin-bottom: 0.8rem;
    }
    &__title-h2 {
        font-size: 1.6rem;
        color: #000;
        line-height: 1.6;
        margin-bottom: 0.8rem;
    }
    &__text {
        font-size: 1.4rem;
        color: #000;
        line-height: 1.6;
    }
    &__btn-wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
        padding-top: 1.2rem;
    }
    &__item {
        > p {
            font-size: 1.2rem;
            color: #000;
            text-align: center;
        }
    }
    &__btn {
        width: 100%;
        background-color: #000;
        color: #fff;
        padding: 1.2rem 0;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 10rem;
        border: 0;
        margin-bottom: 0.8rem;
        font-size: 1.6rem;
    }
    &__row {
        &:not(:last-child) {
            margin-bottom: 1.6rem;
        }
        &.flex {
            display: flex;
            justify-content: center;
        }
        input[type=checkbox] {
            position: absolute;
            left: -9999px;
            box-sizing: border-box;
            padding: 0;
        }
        input[type=checkbox]:not(:checked)+label,
        input[type=checkbox]:checked+label {
            position: relative;
            padding-left: 2rem;
            cursor: pointer;
            height: 2rem;
            line-height: 2rem;
            display: inline-block;
            color: #000;
            font-size: 1.4rem;
        }
        input[type=checkbox]:not(:checked)+label:before,
        input[type=checkbox]:checked+label:before {
            content: '';
            position: absolute;
            left: 0;
            width: 1.8rem;
            height: 1.8rem;
            border: solid 0.2rem #000;
            background-color: transparent;
            top: 0;
            box-sizing: border-box;
        }
        input[type=checkbox]+label:after {
            content: "";
            width: 1.2rem;
            height: 0.9rem;
            background: transparent url(../images/icon-checkbox.svg) no-repeat center center;
            background-size: 1.2rem 0.9rem;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate3d(25%, -60%, 0);
            border: solid 0 transparent;
            transition: all 0.5s ease-in-out;
            opacity: 0;
        }
        input[type=checkbox]:checked+label:after {
            opacity: 1;
        }
    }
}