.section-line-chance {
    background: #000 url('../images/line-chance-bg.jpg') no-repeat center bottom;
    background-size: 41.4rem auto;
    box-sizing: border-box;
    padding: 5rem 2.4rem 36rem;
    height: 94rem;
    padding-left: 0;
    padding-right: 0;
    position: relative;
    &__sub-title {
        width: 24rem;
        margin: 0 auto 1.6rem;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__description {
        width: 24rem;
        margin: 0 auto 1.6rem;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__text {
        font-size: 1.4rem;
        color: #fff;
        text-align: center;
        line-height: 1.6;
        margin-bottom: 1.6rem;
    }
    &__chance {
        text-align: center;
        font-size: 1.4rem;
        color: #fff;
        margin-bottom: 1.6rem;
        strong {
            font-size: 2.4rem;
            padding-right: 0.4rem;
        }
    }
    &__detail {
        position: relative;
        margin-bottom: 2rem;
    }
    &__btn {
        border: solid 0.1rem #fff;
        font-size: 1.4rem;
        width: 7.2rem;
        background-color: transparent;
        display: block;
        margin: 0 auto;
        border-radius: 10rem;
        padding: 0.4rem 0;
        color: #fff;
        transition: all 300ms ease;
        &.active {
            border-color: #000;
            background-color: #000;
        }
    }
    &__btn-close {
        @extend %detail-content-close;
    }
    &__detail-info {
        @extend %detail-content;
        ul {
            list-style: disc;
            margin-left: 1.2rem;
            li {
                font-size: 1.2rem;
                color: #fff;
                line-height: 1.6;
                &:not(:last-child) {
                    margin-bottom: 0.4rem;
                }
                strong {
                    padding: 0 0.4rem;
                }
            }
        }
    }
    &__invitation-code {
        p {
            font-size: 1.4rem;
            text-align: center;
            color: #fff;
            margin-bottom: 0.8rem;
            a {
                color: #fff;
                border-bottom: 0.1rem solid #fff;
                margin-left: 0.4rem;
            }
        }
    }
    &__code {
        display: grid;
        grid-template-columns: 11rem 8.2rem;
        width: 19.2rem;
        margin: 0 auto 2.4rem;
        border-radius: 10rem;
        overflow: hidden;
        > input {
            font-size: 1.6rem;
            color: #471fa7;
            box-sizing: border-box;
            padding: 0;
            border: 0;
            flex-basis: 11rem;
            padding: 1.2rem;
            text-align: center;
            border-radius: 0;
        }
        > button {
            border: 0;
            border-left: solid 0.1rem #7d61c1;
            background-color: #fff;
            flex-basis: 8rem;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 1.6rem;
                margin-right: 0.4rem;
            }
            span {
                font-size: 1.6rem;
                color: #471fa7;
            }
        }
    }
    &__btn-share {
        width: 19.2rem;
        margin: 0 auto 2.4rem;
        background-color: #00ca0e;
        display: flex;
        position: relative;
        justify-content: center;
        align-items: center;
        padding: 1.2rem 0;
        border: 0;
        border-radius: 12rem;
        z-index: 10;
        box-shadow: 0 0.6rem 0 #0a6600;
        > img {
            width: 2rem;
            margin-right: 0.8rem;
        }
        > span {
            font-size: 1.8rem;
            color: #fff;
        }
    }
    &__shoe {
        position: absolute;
        left: 50%;
        transform: translate3d(-50%,0,0);
        bottom: 16%;
        img {
            width: 28rem;
            display: block;
            margin: 0;
        }
    }
}