.rsp-img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
}

%section-title {
    font-size: 3.6rem;
    color: #fff;
    font-weight: 900;
    text-align: center;
    line-height: 1.6;
    @include susy-breakpoint($s-320, $c8) {
        font-size: 2rem;
    }
}

%section-sub-title {
    font-size: 3rem;
    color: #fff;
    font-weight: 900;
    text-align: center;
    line-height: 1.6;
    margin-bottom: 4rem;
    @include susy-breakpoint($s-320, $c8) {
        font-size: 1.8rem;
        margin-bottom: 2.4rem;
    }
}

%detail-content {
    box-sizing: border-box;
    width: 30rem;
    position: absolute;
    top: 4rem;
    background-color: #000;
    border-radius: 2.4rem;
    padding: 4rem 1.6rem 1.6rem;
    left: 50%;
    pointer-events: none;
    opacity: 0;
    transition: all 500ms ease;
    z-index: 100;
    transform: translate3d(-50%,10%,0);
    &.active {
        opacity: 1;
        pointer-events: auto;
        transform: translate3d(-50%,0,0);
    }
    &::before {
        width: 0; 
        height: 0; 
        border-left: 1rem solid transparent;
        border-right: 1rem solid transparent;
        border-bottom: 1rem solid black;
        position: absolute;
        top: -1rem;
        left: 50%;
        transform: translate(-50%,0,0);
        content: "";
    }
    ul {
        list-style: disc;
        margin-left: 1.2rem;
        li {
            font-size: 1.2rem;
            color: #fff;
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
            strong {
                padding: 0 0.4rem;
            }
        }
    }
}

%detail-content-close {
    width: 2.4rem;
    height: 2.4rem;
    border: solid 0.1rem #fff;
    background-color: #000;
    border-radius: 50%;
    position: absolute;
    right: 1.2rem;
    top: 1.2rem;
    &::before,
    &::after {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        width: 1.2rem;
        height: 0.1rem;
        background-color: #fff;
    }
    &::before {
        transform: translate3d(-50%,-50%,0) rotate(45deg);
    }
    &::after {
        transform: translate3d(-50%,-50%,0) rotate(-45deg);
    }
}

%site-sub-title {
    width: 32rem;
    margin: 0 auto 2rem;
    img {
        @extend .rsp-img;
    }
    @include susy-breakpoint($s-320, $c8) {
        width: 24rem;
        margin-bottom: 2.4rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        width: 28rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 22rem;
        margin-bottom: 1.6rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        width: 22rem;
        margin-bottom: 1.6rem;
    }
}

%site-description {
    width: 52rem;
    margin: 0 auto 1.6rem;
    img {
        max-width: 100%;
        display: block;
        margin: 0;
    }
    @include susy-breakpoint($s-320, $c8) {
        width: 24rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        width: 40rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 40rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        width: 40rem;
    }
}

%site-text {
    font-size: 1.8rem;
    color: #fff;
    text-align: center;
    line-height: 1.6;
    margin-bottom: 1.6rem;
    @include susy-breakpoint($s-320, $c8) {
        font-size: 1.4rem;
        br {
            display: none;
        }
    }
    @include susy-breakpoint($s-960, $c12) {
        font-size: 1.4rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        font-size: 1.6rem;
    }
}

%site-btn-w {
    width: 100%;
    background-color: #fff;
    color: #4600a7;
    padding: 1.2rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10rem;
    border: 0;
    font-size: 2.4rem;
    margin-bottom: 1.6rem;
    transition: all 500ms ease;
    &:hover {
        transform: translate3d(0,-5%,0);
    }
    @include susy-breakpoint($s-320, $c8) {
        font-size: 1.4rem;
        margin-bottom: 0.8rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        font-size: 1.6rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        font-size: 1.6rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        font-size: 2rem;
    }
}

%site-btn-wrap {
    width: 40rem;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.4rem;
    @include susy-breakpoint($s-320, $c8) {
        width: 24rem;
        gap: 1.6rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        width: 32rem;
        gap: 2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 36rem;
        gap: 2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        width: 36rem;
        gap: 2rem;
    }
}
