html {
    font-family: "Roboto", "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", sans-serif;
    height: 100%;
    &.fixed {
        overflow: hidden;
    }
}

body {
    background-color: var(--bg-color);
    height: 100%;
    &.fixed {
        overflow: hidden;
    }
}

.site-main {
    position: relative;
}

.wrap {
    position: relative;
    overflow: hidden;
    height: 100vh;
    &.end {
        height: auto;
    }
}

.wrap-line {
    position: relative;
    background: #000 url('../images/line-bg.jpg') repeat-x center top;
    background-size: cover;
}

.btn-gotop {
    background: transparent url('../images/btn-top@3x.png') no-repeat 0 0;
    background-size: cover;
    width: 48px;
    height: 48px;
    position: fixed;
    right: 0;
    bottom: 0;
    z-index: 100;
    padding: 0;
    border: 0;
    transition: all 500ms ease-in-out;
    transform: translate3d(-50%,100%,0);
    &.is-active {
        transform: translate3d(-50%,-50%,0);
    }
    @include susy-breakpoint($s-320, $c8) {
        width: 32px;
        height: 32px;
    }
}