.site-footer {
    background-color: #000;
    &__top {
        padding: 5.6rem 0;
        display: flex;
        align-items: center;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            padding-top: 3.2rem;
            padding-bottom: 3.2rem;
        }
    }
    &__logo {
        width: 6.4rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 3.6rem;
        }
    }
    &__bottom {
        padding: 2rem 0;
        display: flex;
        align-items: center;
        justify-content: center;
        border-top: solid 0.1rem #313131;
    }
    &__copyright {
        font-size: 1rem;
        color: #b2b2b2;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 0.8rem;
        }
    }
}